.portfolio{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 70vh;
    :nth-child(1){
        color: var(--black);
        font-size: 2rem;
        font-weight: bold;
    }
    :nth-child(2){
        color: var(--orange);
        font-size: 2.5rem;
        font-weight: bold;
    }
    img{
        width: 19rem;
        filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
        border-radius: 19px;
    }
}
.portfolio .swiper{
    overflow: visible!important;
}
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;
}
.portfolio .swiper-slide{
    width: 20rem;
}