.intro{
    display: flex;
    height: 77vh;
    margin-top: 6rem;
    .left{
        flex: 1;
        display: flex;
        position: relative;
        flex-direction: column;
        gap: 2rem;
        .name{
            display: flex;
            flex-direction: column;
            :nth-child(1){
                color: var(--black);
                font-weight: bold;
                font-size: 3rem;
            }
            :nth-child(2){
               color: var(--orange); 
               font-weight: bold;
               font-size: 3rem;
            }
            :nth-child(3){
                font-weight: 100;
                font-size: 20px;
                color: var(--gray);
                margin-top: 10px;
            }
        }
        .button{
            border-radius: 34px;
            border: none;
            color: white;
            font-size: 16px;
            padding: 11px 26px 11px 26px;
            background: linear-gradient(100deg,#fdc50f 26.71%,#fb982f 99.36%);
            box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
            width: 6rem;
            height: 2rem;
            &:hover{
                background: white;
                cursor: pointer;
                color: var(--orange);
                border: 1px solid var(--orange);
            }
        }
        .icons{
            margin-top: 3rem;
            display: flex;
            gap: 0rem;
            *{
                transform: scale(0.5);
                &:hover{
                    cursor: pointer;
                }
            }
        }
    }
    .right{
        flex: 1;
        position: relative;
        *{
            position: absolute;
            z-index: 1;
        }
        :nth-child(1){
            transform: scale(0.69);
            left: -15%;
            top: -9%;
        }
        :nth-child(2){
            top: -4.6rem;
            transform: scale(0.67);
            left: -3rem;
        }
        :nth-child(3){
            transform: scale(1.4);
            left: 28%;
        }
        :nth-child(4){
            transform: scale(0.6);
            top: -19%;
            left: -24%;
            border-radius: 50%;
            padding: 0;
        }
        .blur{
            background: rgba(238,210,255,0);
            position: absolute;
            width: 22rem;
            height: 14rem;
            border-radius: 50%;
            background: #edd0ff;
            filter: blur(72px);
            z-index: -9;
            top: -18%;
            left: 56%;
        }
        .blur1{
            background: #C1F5FF;
            position: absolute;
            border-radius: 50%;
            filter: blur(72px);
            z-index: -9;
            top: 17rem;
            width: 21rem;
            height: 11rem;
            left: -9rem;
        }
    }
}
@media screen and (max-width:480px) {
    .intro{
        flex-direction: column;
        height: 64rem;
        gap: 7rem;
        .right{
            transform: scale(0.8);
            left: -3rem;
        }
    }
    .blur{
        display: none;
    }
    .blur1{
        display: none;
    }
}