.service{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
    .awesome{
        display: flex;
        flex-direction: column;
        position: relative;
        :nth-child(1){
            color: var(--black);
            font-size: 2.5rem;
            font-weight: bold;
        }
        :nth-child(2){
            color: var(--orange);
            font-size: 2.5rem;
            font-weight: bold;
        }
        :nth-child(3){
            color: var(--gray);
            font-size: 14px;
            margin-top: 1rem;
        }
        .btn-btn{
            border-radius: 34px;
            border: none;
            color: white;
            font-size: 16px;
            padding: 11px 26px 11px 26px;
            background: linear-gradient(100deg,#fdc50f 26.71%,#fb982f 99.36%);
            box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
            width: 8rem;
            height: 2rem;
            margin-top: 1rem;
            &:hover{
                background: white;
                cursor: pointer;
                color: var(--orange);
                border: 1px solid var(--orange);
            }
        }
        .blur{
            background: #ABF1FF94;
            position: absolute;
            width: 22rem;
            height: 14rem;
            border-radius: 50%;
            background: #edd0ff;
            filter: blur(72px);
            z-index: -9;
            top: 18%;
            left: 200%;
        }
    }
    .cards{
        position: relative;
        >*{
            position: absolute;
        }
    }
}
@media screen and (max-width:480px) {
    .service{
        margin-top: 0;
        flex-direction: column;
        gap: 5rem;
        height: 66rem;
        padding: 0;
        .cards{
            display: flex;
            flex-direction: column;
            gap: 17rem;
            >*{
                position: static;
            }
        }
    }
}