.card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    border-radius: 20px;
    padding: 0 26px 2rem 26px;
    span:nth-of-type(2){
        color: var(--gray);
        font-size: 16px;
    }
    img{
        transform: scale(0.6);
        margin-bottom: -2rem;
    }
    .c-button{
        background: #ffffff;
        box-shadow: 0 19px 60px rgba(0,0,0,0.08);
        border-radius: 7px;
        border: none;
        padding: 10px;
        font-size: 16px;
        color: #5290fd;
        &:hover{
            cursor: pointer;
        }
    }
}
@media screen and (max-width:480px) {
    .card{
        width: auto;
    }   
}