.navbar{
    height: 10vh;
    display: flex;
    justify-content: space-between;
    .left{
        flex: 1;
        display: flex;
        align-items: center;
        gap: 2rem;
        .name{
            font-size: 1.3rem;
            font-weight: bold;
        }
        span{}
    }
    .right{
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 400;
        .list{
            flex: 10;
            ul{
                display: flex;
                gap: 2rem;
                margin-right: 4rem;
                list-style: none;
                &:hover{
                    cursor: pointer;
                }
                li{

                    &:hover{
                        color: var(--orange);
                    }
                }
            }
        }
        .button{
            border-radius: 34px;
            border: none;
            color: white;
            font-size: 16px;
            padding: 11px 26px 11px 26px;
            background: linear-gradient(100deg,#fdc50f 26.71%,#fb982f 99.36%);
            box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
            flex: 2;
            &:hover{
                background: white;
                cursor: pointer;
                color: var(--orange);
                border: 1px solid var(--orange);
            }
        }
    }
}
@media screen and (max-width:480px) {
    .list{
        display: none;
    }
    .button{
        display: none;
    }
}