.footer{
    display: flex;
    align-items: center;
    margin-top: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
    .footer-content{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 100%;
        margin-top: 4rem;
        gap: 4rem;
        color: white;
        .footer-icons{
            display: flex;
            gap: 2rem;
        }
    }
}
@media screen and (max-width:480px){
    .footer{
        .footer-content{
            width: 100%;
            margin-top: 2.5rem;
            gap: 0.5rem;
        }
    }
}