.contact{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 80vh;
    margin-top: 4rem;
    .right{
        display: flex;
        justify-content: center;
        position: relative;
        flex: 1;
        form{
            display: flex;
            flex-direction: column;
            gap: 2rem;
            align-items: center;
            textarea{
                height: 4rem!important;
            }
        }
        .user{
            width: 20rem;
            height: 2rem;
            padding: 0.3rem;
            outline: none;
            border: 2px solid var(--orange);
            border-radius: 8px;
            font-size: 16px;
        }
        .button{
            border-radius: 34px;
            border: none;
            color: white;
            font-size: 16px;
            padding: 11px 26px 11px 26px;
            background: linear-gradient(100deg,#fdc50f 26.71%,#fb982f 99.36%);
            box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
            &:hover{
                background: white;
                cursor: pointer;
                color: var(--orange);
                border: 1px solid var(--orange);
            }
        }
    }
    .left{
        flex: 1;
        .awesome{
            display: flex;
            flex-direction: column;
            :nth-child(1){
                color: var(--black);
                font-weight: bold;
                font-size: 3rem;
            }
            :nth-child(2){
               color: var(--orange); 
               font-weight: bold;
               font-size: 3rem;
            }
        }
    }
}
@media screen and (max-width:480px) {
    .contact{
        flex-direction: column;
        .left{
            display: none;
        }
        .right{
            form{
                .user{
                    width: auto;
                }
            }
        }
    }
}