.works{
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-bottom: 8rem;
    margin-top: 9rem;
    margin-bottom: 13rem;
    .awesome{
        display: flex;
        flex-direction: column;
        position: relative;
        :nth-child(1){
            color: var(--black);
            font-size: 2.5rem;
            font-weight: bold;
        }
        :nth-child(2){
            color: var(--orange);
            font-size: 2.5rem;
            font-weight: bold;
        }
        :nth-child(3){
            color: var(--gray);
            font-size: 14px;
            margin-top: 1rem;
        }
        .btn-btn{
            border-radius: 34px;
            border: none;
            color: white;
            font-size: 16px;
            padding: 11px 26px 11px 26px;
            background: linear-gradient(100deg,#fdc50f 26.71%,#fb982f 99.36%);
            box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
            width: 8rem;
            height: 2rem;
            margin-top: 1rem;
            &:hover{
                background: white;
                cursor: pointer;
                color: var(--orange);
                border: 1px solid var(--orange);
            }
        }
        .blur{
            background: #ABF1FF94;
            position: absolute;
            width: 22rem;
            height: 14rem;
            border-radius: 50%;
            background: #edd0ff;
            filter: blur(72px);
            z-index: -9;
            top: 18%;
            left: 200%;
        }
    }
    .right{
        position: relative;
        .main-circle{
            left: 9rem;
            position: relative;
            width: 18rem;
            height: 18rem;
            border-radius: 100%;
            box-shadow: var(--smboxShadow);
            top: 2rem;
            background: white;
            >*{
                position: absolute;
            }
            :nth-child(1){
                top: -3rem;
                left: 6rem;
            }
            :nth-child(2){
                left: -3rem;
                top: 5rem;
            }
            :nth-child(3){
                top: 5rem;
                left: 6rem;
            }
            :nth-child(4){
                top: 5rem;
                left: 15rem;
            }
            :nth-child(5){
                left: 6rem;
                top: 13rem;
            }
            .sec-circle{
                width: 6rem;
                height: 6rem;
                position: absolute;
                border-radius: 100%;
                border: 5px solid #ebebeb;
                display: flex;
                align-items: center;
                justify-content: center;
                background: none;
                box-shadow: var(--smboxShadow);
                background: white;
                img{
                    transform: scale(0.6);
                }
            }
        }
    }
}
@media screen and (max-width:480px) {
    .works{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        gap: 5rem;
        .right{
            display: flex;
            align-items: flex-start;
            justify-content: start;
            transform: scale(0.7);
            .main-circle{
                position: static;
            }
        }
    }   
}