.testimonials{
    padding: 0 3rem 0 3rem;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    gap: 2rem;
    position: relative;
    .heading{
        align-self: start;
        >*{
            font-size: 2rem;
            font-weight: bold;
        }
        :nth-child(2){
            color: var(--orange);
        }
    }
    .reviews{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        padding: 2rem;
        width: 30rem;
        height: 12rem;
        background: rgba(255, 255, 255, 0.26);
        border: 7px solid var(--blueCard);
        box-shadow: var(--boxShadow);
        border-radius: 20px;
        img{
            width: 6rem;
            height: 6rem;
            border-radius: 100%;
        }
        >span{
            color: var(--gray);
        }
    }
    .swiper{
        width: 100%;
        height: 70%;
    }
    .swiper-slide{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .swiper-pagination-bullet-active{
        background: var(--orange);
    }
}
@media screen and (max-width:480px) {
    .testimonials{
        padding: 0;
        img{
            width: 70px;
            height: 70px;
        }
    }   
}